import forumFooter from "../../components/forumfooter.vue";
var app = require("../../common/common.js");
var per_page = 0;
var isfirst = true;
var gid = 0;
var activeClass = "tabs-border-active";
export default {
	components:{
		forumFooter
	},
	data: function() {
		return {
			defaultActive: "tabs-border-active",
			pageLoad: false,
			pageHide: false,
			pageData: {},
		}

	},
	onLoad: function(option) {
		uni.setNavigationBarTitle({
			title: '校园云生活'
		});
		this.getPage();
	},


	onReachBottom: function() {
		this.getList();
	},
	onPullDownRefresh: function() {
		this.refresh();
	},
	methods: {
		gourl:function(url){
			uni.navigateTo({
				url:url
			})
		},
		getPage: function() {
			var that = this;
			that.app.get({
				url: that.app.apiHost + "/module.php?m=forum&ajax=1",
				
				success: function(res) {
					//登录
					if (res.error == 1000) {
						uni.navigateTo({
							url: "../../pages/login/index",
						})
					} else {
						isfirst = false;
						that.pageLoad = true;
						that.pageData = res.data;
						per_page = res.data.per_page;
					}

				}
			})
		},
		setGroup: function(cid) {
			gid = cid;
			isfirst = true;
			per_page = 0;
			if (gid == 0) {
				this.defaultActive = activeClass;
			} else {
				this.defaultActive = "";
			}
			var grouplist = this.pageData.grouplist;
			for (var i in grouplist) {
				if (grouplist[i].gid == gid) {
					grouplist[i].isactive = 1;
				} else {
					grouplist[i].isactive = 0;
				}
			}
			this.pageData.grouplist = grouplist;
			this.getList();
		},
		getList: function() {
			var that = this;
			if (!isfirst && per_page == 0) return false;
			uni.request({
				url: app.apiHost + "/module.php?m=forum&ajax=1",
				data: {
					per_page: per_page,
					gid: gid,
					authcode: app.getAuthCode()
				},
				success: function(res) {

					if (!res.data.error) {
						if (isfirst) {
							that.pageData.list = res.data.data.list;
							isfirst = false;
						} else {

							that.pageData.list = app.json_add(that.pageData.list, res.data.data.list);
						}
						per_page = res.data.data.per_page;

					}


				}
			})
		},
		goForum: function(id) {
			uni.navigateTo({
				url: "../../pageforum/forum/show?id=" + id
			})
		},
		refresh: function() {
			this.getPage();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000)
		},
		loadMore: function() {
			this.getList();
		}
	},
}
